.container{
    font-size: .6rem;
    width: calc(1rem*17);
    height: calc(1rem*22);
}
@media (min-width:500px) {
    .container{
        font-size: 0.875rem;
    }
} 
@media (min-width:800px) {
    .container{
        font-size: 1rem;
    }
}
.crab{
    height: 9em;
    width: 16em;    
    /* background-color: red; */
    border-radius: 0 0 6em 6em;
    box-shadow: -0.6em 0.25em 0 0.25em ;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    animation: move 3s alternate infinite ease-in-out;
}

@keyframes move {
    0% {
        left: -3em;
    }
    100% {
        left: 3em;
    }
}

.hands {
    height: 6.25em;
    width: 20.25em;
    border: 0.7em solid ;
    position: absolute;
    left: -2.6em;
    bottom: 6.1em;
    z-index: -1;
    border-radius: 0 0 9.37em 9.37em;
    box-shadow: -.1em 0.1em 0.1em .01em rgba(0, 0, 0, .2);
    border-top: none;
  }

.claw-1-l, .claw-1-r, .claw-2-l, .claw-2-r{
    height: 4em;
    width: 2em;
    /* background-color: red; */
    position: absolute;
    top: -3.6em;
    transform-origin: 50% 100%;
}
.claw-1-l,.claw-2-l{
    border-radius: 2.5em 0 0 2.5em;
    transform: rotate(0deg);
    animation: claw-l 3s alternate infinite ease-in-out;
}
@keyframes claw-l {
    100%{
        transform: rotate(-15deg);
    }
}
.claw-1-r,.claw-2-r{
    border-radius:  0 2.5em 2.5em 0;
    transform: rotate(0deg);
    animation: claw-r 3s alternate infinite ease-in-out;
}
@keyframes claw-r {
    100%{
        transform: rotate(15deg);
    }
}
.claw-1-l{
    left: -2.4em;
}
.claw-1-r{
    left: -0.35em;
}
.claw-2-l{
    right: -0.35em;
}
.claw-2-r{
    right: -2.4em;
}

.eye-l,.eye-r{
    height: 3.2em;
    width: 3.2em;
    background-color: black;
    border: .6em solid white;
    outline: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 50%;
    position: absolute;
    top: -3.2em;
}
.eye-l{
    left: 3.5em;
}
.eye-r{
    right: 3.5em;
}

.mouth{
    height: 1em;
    width: 2.5em;
    background-color: rgba(0, 0, 0, 0.2);
    border: .35em solid ;
    border-top: none;
    border-radius: 0em 0 2em 2em;
    position: absolute;
    top: 2em;
    left: 6.7em;
    animation: mouth 1s infinite alternate;
}

@keyframes mouth {
    100% {
        /* background-color: transparent; */
        border-radius: 0 0 .7em .7em;
    }
}

.legs-1,.legs-2,.legs-3{
    width: .85em;
    /* background-color: #99151b; */
    position: absolute;
    z-index: -1;
    border-radius: 0 0 1em 1em;
}
.legs-1{
    height: 8em;
    top: .5em;
    left: -.7em;
    box-shadow: 15.5em 0 0 ;
    animation: legs 1s infinite alternate-reverse ease-in-out;
}
.legs-2{
    height: 9.5em;
    top: 1.5em;
    left: .8em;
    box-shadow: 12.5em 0 0 ;
    animation: legs 1s infinite alternate ease-in-out;
}
.legs-3{
    height: 11.5em;
    top: 1.2em;
    left: 3.2em;
    box-shadow: 8em 0 0 ;
    animation: legs 1s infinite alternate-reverse ease-in-out;
}
@keyframes legs {
    100%{
        top: 0;
    }
}